<template>
  <div class="hero" ref="hero">
    <div class="hero__inner">
      <h1 class="display-1">
        Hey! I'm Rhys.<span class="br"></span>
        I make things<span class="br"></span>
        for the web.
      </h1>
      <a href="#projects-home" class="hero__scroll-down" v-on:click="handleScroll">
        <IconDownArrow fill="#1A2B2B"></IconDownArrow>
      </a>
    </div>
  </div>
</template>

<script>
// Icons
import IconDownArrow from '../icons/IconDownArrow'

// Debounce
import debounce from 'lodash/debounce'

export default {
  name: 'Hero',
  components: {
    IconDownArrow
  },
  mounted () {
    this.setHeroHeight()
    this.handleResize()
  },
  methods: {
    handleScroll: function (e) {
      e.preventDefault()

      const href = e.target.getAttribute('href')
      const offsetTop = document.querySelector(href).offsetTop

      scroll({
        top: offsetTop,
        behavior: 'smooth'
      })
    },
    setHeroHeight: function () {
      const windowHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight

      const windowWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth

      if (windowWidth < 510) {
        this.$refs.hero.style.height = windowHeight - 60 + 'px'
      } else {
        this.$refs.hero.style.height = windowHeight - 80 + 'px'
      }
    },
    handleResize: function () {
      const heroRef = this.$refs.hero

      window.addEventListener('resize', debounce(function () {
        const windowHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight

        const windowWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth

        if (windowWidth < 510) {
          heroRef.style.height = windowHeight - 60 + 'px'
        } else {
          heroRef.style.height = windowHeight - 80 + 'px'
        }
      }, 250, { leading: true, trailing: true }))
    }
  }
}
</script>

<style lang="scss">
// ==============================
// Hero
// ========================================

.hero {
  &__inner {
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    @include mq($from: tablet) {
      height: 100%;
      justify-content: flex-start;
    }
  }
}

// ==============================
// Hero Heading
// ========================================

.hero h1 {
  margin-top: 60px;

  @include mq($until: tablet) {
    margin-top: -20px;
    text-align: center;
  }
}

// ==============================
// Hero Down Arrow
// ========================================

.hero__scroll-down {
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
  animation: heroScrollDownAnimation 2s infinite;
  animation-timing-function: cubic-bezier(.74,.28,.57,.82);

  .icon--down-arrow {
    width: 42px;
    height: 42px;
    pointer-events: none;

    @include mq($from: tablet) {
      width: 54px;
      height: 54px;
    }

    @include mq($from: desktop) {
      width: 62px;
      height: 62px;
    }
  }
}

@keyframes heroScrollDownAnimation {
  0% {
    bottom: 20px;
  }
  25% {
    bottom: 0px;
  }
  60% {
    bottom: 20px;
  }
}

</style>
